const API_BASE_URL = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:3001';

export interface RwaToken {
  tokenId: number;
  balance: string;
  rwaType: string;
  percent: string;
  metadataURI: string;
  // Bientôt, nous ajouterons les metadonnées elles-mêmes
  // metadata?: Record<string, any>; 
}

export interface UserTokensResponse {
  address: string;
  tokens: RwaToken[];
}

/**
 * Récupère les tokens RWA pour une adresse utilisateur donnée.
 * @param address L'adresse du portefeuille de l'utilisateur.
 * @returns Une promesse qui se résout avec la liste des tokens de l'utilisateur.
 */
export async function getUserTokens(address: string): Promise<UserTokensResponse> {
  if (!address) {
    console.error("L'adresse de l'utilisateur est requise pour récupérer les tokens.");
    return { address: '', tokens: [] };
  }

  try {
    const response = await fetch(`${API_BASE_URL}/user/${address}/tokens`);

    if (!response.ok) {
      const errorData = await response.json();
      console.error('Erreur lors de la récupération des tokens:', errorData.error || response.statusText);
      throw new Error(`Erreur API: ${errorData.error || response.statusText}`);
    }

    const data: UserTokensResponse = await response.json();
    
    // À l'avenir, nous pourrions vouloir récupérer les métadonnées de chaque token ici.
    // Par exemple, en faisant une boucle sur data.tokens et en appelant fetch(token.metadataURI)

    return data;

  } catch (error) {
    console.error("Impossible de se connecter à l'API RWA:", error);
    // Retourner un état vide en cas d'erreur réseau ou autre
    return { address: address, tokens: [] };
  }
}

export interface MintRwaTokenPayload {
  to: string;
  amount: number;
  rwaType: string;
  percent: number;
  metadataURI: string;
}

export interface MintRwaTokenResponse {
  success: boolean;
  transactionHash: string;
  blockNumber: number;
  gasUsed: string;
}

/**
 * Crée (minte) un nouveau token RWA.
 * @param payload Les données nécessaires pour la création du token.
 * @returns Une promesse qui se résout avec les informations de la transaction.
 */
export async function mintRwaToken(payload: MintRwaTokenPayload): Promise<MintRwaTokenResponse> {
  try {
    const response = await fetch(`${API_BASE_URL}/mint`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(payload),
    });

    const data = await response.json();

    if (!response.ok) {
      console.error('Erreur lors du minting du token:', data.error || response.statusText);
      throw new Error(`Erreur API: ${data.error || 'Une erreur inconnue est survenue'}`);
    }

    return data;
  } catch (error) {
    console.error("Impossible de se connecter à l'API RWA pour le minting:", error);
    throw error;
  }
} 